"use client";
import { useEffect } from 'react';
// ÿßÿ≥ÿ™ÿÆÿØÿßŸÖ @ Ÿäÿ∂ŸÖŸÜ ÿßŸÑŸàÿµŸàŸÑ ŸÑŸÑŸÖÿ¨ŸÑÿØ ÿßŸÑÿµÿ≠Ÿäÿ≠ ŸÖŸáŸÖÿß ŸÉÿßŸÜ ŸÖÿ≥ÿ™ŸàŸâ ÿßŸÑŸÖŸÑŸÅ
import { useFieldExpert } from "@/hooks/useFieldExpert";

export default function Home() {
    const { videoRef, canvasRef, status, startListening, lastResponse } = useFieldExpert();

    useEffect(() => {
        // ÿ™ÿ¥ÿ∫ŸäŸÑ ÿßŸÑŸÉÿßŸÖŸäÿ±ÿß ÿ™ŸÑŸÇÿßÿ¶ŸäÿßŸã ÿπŸÜÿØ ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑÿµŸÅÿ≠ÿ©
        if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
            navigator.mediaDevices.getUserMedia({ video: { facingMode: "environment" } })
                .then(stream => {
                    if (videoRef.current) videoRef.current.srcObject = stream;
                })
                .catch(err => console.error("Camera error:", err));
        }
    }, [videoRef]);

    return (
        <main className="flex min-h-screen flex-col items-center bg-zinc-950 text-white p-4">
            <div className="w-full max-w-md space-y-4">
                <h1 className="text-center font-black text-blue-500 tracking-tighter text-2xl uppercase">Expert Field Assistant</h1>

                <div className="relative rounded-[2.5rem] border-4 border-blue-600 overflow-hidden aspect-video shadow-2xl bg-black">
                    <video ref={videoRef} autoPlay playsInline muted className="w-full h-full object-cover" />
                    <canvas ref={canvasRef} className="hidden" width="640" height="480" />

                    <div className="absolute bottom-4 left-4 flex items-center gap-2 bg-black/50 px-3 py-1 rounded-full">
                        <div className={`w-3 h-3 rounded-full ${status === "Listening" ? "bg-green-500 animate-pulse" : "bg-red-500"}`} />
                        <span className="text-[10px] font-bold uppercase tracking-widest">{status}</span>
                    </div>
                </div>

                <button
                    onClick={startListening}
                    className="w-full py-5 rounded-2xl bg-blue-600 font-black text-lg hover:bg-blue-700 active:scale-95 transition-all shadow-lg flex items-center justify-center gap-3"
                >
                    {status === "Idle" ? "üé§ ÿ™ŸÅÿπŸäŸÑ ÿßŸÑŸÖÿ≥ÿßÿπÿØ ÿßŸÑÿµŸàÿ™Ÿä" : "üì° ÿßŸÑŸÖÿ≥ÿßÿπÿØ Ÿäÿ≥ÿ™ŸÖÿπ ÿßŸÑÿ¢ŸÜ..."}
                </button>

                <div className="bg-zinc-900 p-6 rounded-[2rem] border border-zinc-800 shadow-inner min-h-[150px]">
                    <p className="text-blue-400 text-[10px] font-black uppercase mb-3 tracking-widest text-right">ÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑÿÆÿ®Ÿäÿ±:</p>
                    <div className="text-right text-lg leading-relaxed font-medium text-zinc-200">
                        {status === "Analyzing" ? (
                            <div className="flex justify-end items-center gap-2">
                                <span>ÿ¨ÿßÿ±Ÿä ÿ™ÿ≠ŸÑŸäŸÑ ÿßŸÑÿ™ŸàÿµŸäŸÑÿßÿ™...</span>
                                <div className="w-2 h-2 bg-blue-500 animate-ping rounded-full" />
                            </div>
                        ) : (lastResponse || "ÿßÿ∂ÿ∫ÿ∑ ÿπŸÑŸâ ÿßŸÑÿ≤ÿ± Ÿàÿßÿ≥ÿ£ŸÑ: 'ŸÖÿß ÿ≠ÿßŸÑÿ© ÿßŸÑÿπÿØÿßÿØÿü'")}
                    </div>
                </div>
            </div>
        </main>
    );
}